import { Link } from "gatsby"
import React from "react"

import "./rightright_logo.svg"

const Header = ({ siteTitle, menu, location }) => {
  return (
  <header class="header" role="banner">
    <div class="grid">

      <div class="unit golden-small">
        <h1 class="logo">
          <Link
            to="/"
          >
            {siteTitle}
          </Link>
        </h1>
      </div>

      <div class="unit golden-big">
        <nav class="nav">
          {menu
          .filter(item => item.path != location.pathname)
          .map(item => (
            <Link
              to={item.path}
              class="nav-link"
            >
              {item.label}
            </Link>
          ))}
        </nav>
      </div>

    </div>

    <div class="grid">
      <p class="unit whole description">
        i am <a href="#contacts">giuseppe sorrentino</a> and this is my personal digital playground
      </p>
    </div>

  </header>
)}

export default Header
